<template>
  <v-container fluid class="px-5">
    <v-row class="mb-3" justify="space-between">
      <v-col cols="12" sm="6" md="4" xl="2" align-self="end">
        <v-tooltip v-if="userRole == 'Admin'" top>
          <template v-slot:activator="{ on, attrs }">
            <v-btn
              icon
              color="blue darken-3 white--text"
              @click="showAddModal"
              v-bind="attrs"
              v-on="on"
              large
              class="mr-1"
            >
              <v-icon>mdi-plus</v-icon>
            </v-btn>
          </template>
          <span>Add User</span>
        </v-tooltip>
        <v-tooltip top>
          <template v-slot:activator="{ on, attrs }">
            <v-btn
              icon
              color="secondary"
              @click="handleFilter"
              v-bind="attrs"
              v-on="on"
              class="mr-1"
            >
              <v-icon>mdi-cached</v-icon>
            </v-btn>
          </template>
          <span>Refresh Users</span>
        </v-tooltip>
      </v-col>
      <v-col cols="12" sm="6" md="4" xl="3">
        <v-text-field
          v-model="search"
          prepend-inner-icon="mdi-magnify"      
          label="Search"
          single-line
          hide-details="auto"
          :disabled="isLoading"
          clearable
        >
        </v-text-field>
      </v-col>
    </v-row>
    <!-- <v-row class="mt-1" justify="space-between">
      <v-col sm="5" md="4" xl="3">
