          label="Search"
          single-line
          hide-details="auto"
          :disabled="isLoading"
        ></v-text-field>
      </v-col>
    </v-row> -->
    <v-data-table
      :headers="headers"
      :items="records"
      item-key="id"
      class="elevation-1"
      :search="search"
      :loading="isLoading"
      loading-text="Loading... Please wait"
      :sort-by.sync="sortBy"
      :sort-desc.sync="sortDesc"
    >
      <template v-slot:item.actions="{ item }" v-if="userRole == 'Admin'">
        <v-tooltip right>
          <template v-slot:activator="{ on, attrs }">
            <v-btn
              v-bind="attrs"
              v-on="on"
              @click="viewInfo(item)"
              icon
              small
            >
              <v-icon
                size="large"
                color="blue darken-3"
              >
                mdi-note-edit-outline
              </v-icon>
            </v-btn>
          </template>
          <span>View / Update</span>
        </v-tooltip>

        <v-tooltip right>
          <template v-slot:activator="{ on, attrs }">
            <v-btn
              v-bind="attrs"
              v-on="on"
              @click="resetPassword(item)"
              icon
              small
            >
              <v-icon
                size="large"
                color="blue darken-3"
              >
                mdi-lock-reset
              </v-icon>
            </v-btn>
          </template>
          <span>Reset Password</span>
        </v-tooltip>
      </template>

      <template v-slot:item.is_active="{ item }">
        <v-chip
          :color="item.is_active=='Active' ? 'success' : 'red'"
          dark
          small
          label
        >
          <v-icon 
            v-if="item.is_active=='Active'" 
            left
            size="medium"
          >
            mdi-check
          </v-icon>
          <v-icon 
            v-else 
            left
            size="medium"
          >
            mdi-close
          </v-icon>
          {{ item.is_active }}
        </v-chip>
      </template>
    </v-data-table>
    <ViewInfoModal v-if="viewInfoModal" :modalData="viewInfoModalData" @closeModal="viewInfoModal = false" @refresh="handleFilter" />
    <ResetPassModal v-if="resetPassModal" :modalData="resetPassModalData" @closeModal="resetPassModal = false" />
    <AddModal v-if="addModal" :modalData="addModalData" @closeModal="addModal = false" @refresh="handleFilter" />
  </v-container>
</template>

<script>
import { mapState, mapActions, mapGetters } from "vuex"
import AddModal from "@/components/users/AddModal"
import ViewInfoModal from "@/components/users/ViewInfoModal"
import ResetPassModal from "@/components/users/ResetPassModal"

export default {
  components: { AddModal, ViewInfoModal, ResetPassModal },
  data: () => ({
    search: '',
    sortBy: 'created_at',
    sortDesc: true,
    headers: [
      { text: 'Status', value: 'is_active' },
      { text: 'Name', value: 'name' },
      { text: 'Email', value: 'email' },
      { text: 'Role', value: 'role' },
      { text: 'Created At', value: 'created_at' },
      { text: 'Updated At', value: 'updated_at' },
    ],
    viewInfoModal: false,
    viewInfoModalData: [],
    resetPassModal: false,
    resetPassModalData: [],
    addModal: false,
    addModalData: [],
  }),
  computed: {
    ...mapGetters(["userRole"]),
    ...mapState("users", ["records", "isLoading"]),
  },
  created() {
    this.handleFilter()

    if (this.userRole=='Admin') {
      this.headers.unshift({ 
        text: 'Actions', 
        value: 'actions', 
        sortable: false,
        align: 'center'
      })
    }
  }, 
  methods: {
    ...mapActions("users", ["getRecords"]),
    handleFilter() {
      if (!this.isLoading) {
        this.$store.commit("users/setRecords", [])
        this.getRecords()
      }
    },
    viewInfo(item) {
      this.viewInfoModal = true
      this.viewInfoModalData = { ...item, dialog: this.viewInfoModal }
    },
    resetPassword(item) {
      this.resetPassModal = true
      this.resetPassModalData = { ...item, dialog: this.resetPassModal }
    },
    showAddModal() {
      this.addModal = true
      this.addModalData = { dialog: this.addModal }
    },
  }
}
</script>