<template>
  <v-dialog
    v-model="dialog"
    width="1000px"
  >
    <v-card>
      <v-card-title>
        <span class="text-h6">Update User</span>
      </v-card-title>
      <v-divider></v-divider>
      <v-card-text class="pt-3 pb-6">
        <v-alert
          :value="!!updateError"
          dense
          outlined
          type="error"
          transition="scale-transition"
          icon="mdi-exclamation-thick"
          class="mb-3"
        >
          {{ updateError }}
        </v-alert>
        <v-form
          ref="form"
          v-model="valid"
          @submit.prevent="handleSubmit"
        >
          <div class="mb-2">
            <small>* indicates required field</small>
          </div>
          <v-row>
            <v-col
              cols="12"
              sm="6"
              md="4"
            >
              <v-text-field
                ref="surname"
                :rules="[rules.required, rules.minimum]"
                v-model="surname"
                label="Surname*"
                counter="30"
                maxlength="30"
                hide-details="auto"
              ></v-text-field>
            </v-col>
            <v-col
              cols="12"
              sm="6"
              md="4"
            >
              <v-text-field
                ref="given_name"
                :rules="[rules.required, rules.minimum]"
                v-model="given_name"
                label="Given Name*"
                counter="30"
                maxlength="30"
                hide-details="auto"
              ></v-text-field>
            </v-col>
            <v-col
              cols="12"
              sm="6"
              md="4"
            >
              <v-text-field
                ref="middle_name"
                :rules="[rules.maximumOptional]"
                v-model="middle_name"
                label="Middle Name*"
                counter="30"
                maxlength="30"
                hide-details="auto"
              ></v-text-field>
            </v-col>
            <v-col cols="12" md="12">
              <v-text-field
                ref="email"
                :rules="[rules.required, rules.validEmail, emailExist]"
                v-model="email"
                label="Email*"
                hide-details="auto"
                required
              ></v-text-field>
            </v-col>
          </v-row>
          <div class="mt-6">
            <v-switch
              v-model="status"
              :label="`Status: ${statusText}`"
            ></v-switch>
          </div>
          <div>
            <v-btn
              :disabled="!valid"
              type="submit"
              color="blue darken-3 white--text"
              class="mr-2 mt-2"
              :loading="updateLoading"
              depressed
            >
              Save
            </v-btn>
            <v-btn
              @click="closeModal"
              color="secondary"
              class="mt-2"
              depressed
              outlined
            >
              Cancel
            </v-btn>
          </div>
        </v-form>
      </v-card-text>
    </v-card>
  </v-dialog>
</template>

<script>
import { mapState, mapActions } from "vuex"
import { isObjectEmpty } from '@/helpers/form'

export default {
  props: {
    modalData: {
      type: Object,
      required: true
    }
  },
  data: () => ({
    valid: true,
    rules: {
      required: v => !!v || 'This field is required.',
      minimum: v => (v && v.length >= 2) && (v && v.length <= 30) || 'Minimum 2 - 30 characters.',
      maximumOptional: v => {
        if (v) return v.length <= 30 || 'Maximum 30 characters.'
        else return true
      },
      validEmail: v => /.+@.+\..+/.test(v) || 'E-mail must be valid'
    },
    surname: '',
    given_name: '',
    middle_name: '',
    email: '',
    emailExistText: '',
    status: false
  }),
  computed: {
    ...mapState("users", ["updateLoading", "updateError", "updateErrors"]),
    dialog: {
      get: function () {
        return this.modalData.dialog
      },
      // setter
      set: function () {
        this.closeModal()
      }
    },
    statusText() {
      return this.status ? 'Active' : 'Inactive'
    }
  },
  mounted() {
    this.$refs.surname.focus()
    this.surname = this.modalData.last_name
    this.given_name = this.modalData.first_name
    this.middle_name = this.modalData.middle_name
    this.email = this.modalData.email
    this.status = this.modalData.is_active=='Active' ? true : false
  },
  beforeDestroy() {
    this.$store.commit("users/setUpdateError", null)
  },
  methods: {
    ...mapActions("users", ["updateRecord"]),
    emailExist(val) {
      if (val == this.emailExistText) {
        return `"${val}" already exist`
      } else {
        return true
      }
    },
    async handleSubmit() {
      if (!this.updateLoading) {
        await this.updateRecord({ id: this.modalData.id, surname: this.surname, given_name: this.given_name, middle_name: this.middle_name, email: this.email, status: this.status })

        if (!this.updateError && isObjectEmpty(this.updateErrors)) {
          // reset form
          this.$emit('refresh')

          this.closeModal()
        } else {
          if (!isObjectEmpty(this.updateErrors)) {
            this.emailExistText = this.email
            this.$refs['email'].focus()
     
            this.$refs.form.validate()
          } else {
            this.$refs.form.resetValidation()
          }
        }
      }
    },
    closeModal() { 
      this.$emit('closeModal')
    }
  }
}
</script>