<template>
  <v-dialog
    v-model="dialog"
    width="1000"
  >
    <v-card>
      <v-card-title>
        <span class="text-h6">Add User</span>
      </v-card-title>
      <v-divider></v-divider>
      <v-card-text class="pt-3 pb-6">
        <v-alert
          :value="!!addError"
          dense
          outlined
          type="error"
          transition="scale-transition"
          icon="mdi-exclamation-thick"
          class="mb-3"
        >
          {{ addError }}
        </v-alert>
        <v-form
          ref="form"
          v-model="valid"
          @submit.prevent="handleSubmit"
        >
          <div class="mb-2">
            <small>* indicates required field</small>
          </div>
          <v-row>
            <v-col
              cols="12"
              sm="6"
              md="4"
            >
              <v-text-field
                ref="surname"
                :rules="[rules.required, rules.minimum]"
                v-model="surname"
                label="Surname*"
                counter="30"
                maxlength="30"
                hide-details="auto"
              ></v-text-field>
            </v-col>
            <v-col
              cols="12"
              sm="6"
              md="4"
            >
              <v-text-field
                ref="given_name"
                :rules="[rules.required, rules.minimum]"
                v-model="given_name"
                label="Given Name*"
                counter="30"
                maxlength="30"
                hide-details="auto"
              ></v-text-field>
            </v-col>
            <v-col
              cols="12"
              sm="6"
              md="4"
            >
              <v-text-field
                ref="middle_name"
                :rules="[rules.maximumOptional]"
                v-model="middle_name"
                label="Middle Name*"
                counter="30"
                maxlength="30"
                hide-details="auto"
              ></v-text-field>
            </v-col>
            <v-col cols="12" md="12">
              <v-text-field
                ref="email"
                :rules="[rules.required, rules.validEmail, emailExist]"
                v-model="email"
                label="Email*"
                hide-details="auto"
                required
              ></v-text-field>
            </v-col>
          </v-row>
          <div class="mt-5">
            <v-btn
              :disabled="!valid"
              type="submit"
              color="blue darken-3 white--text"
              class="mr-2 mt-2"
              :loading="addLoading"
              depressed
            >
              Add
            </v-btn>
            <v-btn
              @click="closeModal"
              color="secondary"
              class="mt-2"
              depressed
              outlined
            >
              Cancel
            </v-btn>
          </div>
        </v-form>
      </v-card-text>
    </v-card>
  </v-dialog>
</template>

<script>
import { mapState, mapActions } from "vuex"
import { isObjectEmpty } from '@/helpers/form'

export default {
  props: {
    modalData: {
      type: Object,
      required: true
    }
  },
  data: () => ({
    valid: true,
    rules: {
      required: v => !!v || 'This field is required.',
      minimum: v => (v && v.length >= 2) && (v && v.length <= 30) || 'Minimum 2 - 30 characters.',
      maximumOptional: v => {
        if (v) return v.length <= 30 || 'Maximum 30 characters.'
        else return true
      },
      validEmail: v => /.+@.+\..+/.test(v) || 'E-mail must be valid'
    },
    surname: '',
    given_name: '',
    middle_name: '',
    email: '',
    emailExistText: '',
  }),
  computed: {
    ...mapState("users", ["addLoading", "addError", "addErrors"]),
    dialog: {
      get: function () {
        return this.modalData.dialog
      },
      // setter
      set: function () {
        this.closeModal()
      }
    },
  },
  mounted() {
    this.$refs.surname.focus()
  },
  methods: {
    ...mapActions("users", ["addRecord"]),
    emailExist(val) {
      if (val == this.emailExistText) {
        return `"${val}" already exist`
      } else {
        return true
      }
    },
    async handleSubmit () {
      if (!this.addLoading) {
        await this.addRecord({ surname: this.surname, given_name: this.given_name, middle_name: this.middle_name, email: this.email })

        if (!this.addError && isObjectEmpty(this.addErrors)) {
          // reset form
          this.$refs.form.reset()
          // this.$refs.surname.focus()
          this.$emit('refresh')
          this.closeModal()
        } else {
          if (!isObjectEmpty(this.addErrors)) {
            this.emailExistText = this.email
            this.$refs['email'].focus()
     
            this.$refs.form.validate()
          } else {
            this.$refs.form.resetValidation()
          }
        }
      }
    },

    closeModal() { 
      this.$emit('closeModal')
    }
  },
  /* watch: {
    dialog(visible) {
      if (visible) {
        setTimeout(() => {
          this.$refs.surname.focus()
        }, 200);
      } else {
        // remove error
        this.$store.commit("users/setAddError", null)
      }
    }
  }, */
}
</script>