<template>
  <v-dialog
    v-model="dialog"
    width="600px"
  >
    <v-card>
      <v-card-title>
        <span class="text-h6">Reset Password</span>
      </v-card-title>
      <v-divider></v-divider>
      <v-card-text class="pt-3 pb-6">
        <v-alert
          :value="!!resetPassError"
          dense
          outlined
          type="error"
          transition="scale-transition"
          icon="mdi-exclamation-thick"
          class="mb-3"
        >
          {{ resetPassError }}
        </v-alert>
        <p class="text-subtitle-1">An email with new password will be sent to {{ modalData.email }}.</p>
        <p class="text-subtitle-1">Are you sure you want to reset the password of {{ modalData.name }}?</p>
        <v-btn
          :disabled="resetPassLoading"
          type="submit"
          color="blue darken-3 white--text"
          class="mr-2 mt-2"
          :loading="resetPassLoading"
          depressed
          @click="handleClick"
        >
          Reset
        </v-btn>
        <v-btn
          @click="closeModal"
          color="secondary"
          class="mt-2"
          depressed
          outlined
        >
          Cancel
        </v-btn>
      </v-card-text>
    </v-card>
  </v-dialog>
</template>

<script>
import { mapState, mapActions } from "vuex"

export default {
  props: {
    modalData: {
      type: Object,
      required: true
    }
  },
  computed: {
    ...mapState("users", ["resetPassLoading", "resetPassError"]),
    dialog: {
      get: function () {
        return this.modalData.dialog
      },
      // setter
      set: function () {
        this.closeModal()
      }
    }
  },
  beforeDestroy() {
    this.$store.commit("users/setResetPassError", null)
  },
  methods: {
    ...mapActions("users", ["resetPassword"]),
    async handleClick() {
      if (!this.resetPassLoading) {
        await this.resetPassword({ id: this.modalData.id, email: this.modalData.email })

        if (!this.resetPassError) {
          // reset form
          this.$emit('refresh')

          this.closeModal()
        }
      }
    },
    closeModal() { 
      this.$emit('closeModal')
    }
  }
}
</script>